.course-content-modal {
	width: 100vw;
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.2);
	display: grid;
	place-items: center;
	z-index: 1000;
}

.course-modal {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 100%;
	max-width: 600px;
	min-height: auto;
	height: fit-content;
	background-color: white;
}

.course-content-modal label {
	margin: 10px 0;
}

.course-content-modal::placeholder {
	color: rgba(0, 0, 0, 0.6) !important;
}
.course-content-modal select {
	outline: none;
	cursor: pointer;
}
.course-modal label p {
	color: #000 !important;
}
.create-course-modal input[type='text'] {
	padding: 10px;
}
.create-course-modal input[type='file'] {
	border: none;
	padding-left: 0;
}
.create-course-modal select {
	padding: 10px;
}
.video-content > * {
	margin: 10px 0;
}

.course-modal .modal-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 20px;
}

.course-modal button {
	background-color: rgb(0, 140, 255);
	color: #fff;
	border-radius: 5px;
	border: none;
	outline: none;
	cursor: pointer;
	padding: 5px 10px;
	width: 130px;
	margin: 0 10px;
	font-size: 15px;
	font-weight: 500;
}
.course-loader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: rgba(255, 255, 255, 0.4);
	display: grid;
	place-items: center;
	z-index: 9999;
  }
  .course-loader div {
	margin-right: 25px;
  }