}

.schedule-class-modal-card .close-btn {
  position: absolute;
  top: 0;
  right: 0;
  margin: 10px 10px 0 0;
  display: grid;
  place-items: center;
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  border-radius: 50%;
  padding: 4px;
}
