.my-quiz {
  padding: 10px 20px 20px 20px;
}

.loading-div {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
  z-index: 999;
}

.Enrolled-courses .title h1 {
  font-size: 24px;
  font-weight: 600;
  margin: 15px 0;
}

.Enrolled-courses .title h2 {
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 10px;
}

.title .stats {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 40px 0 60px 0;
}

.title .stats .card {
  box-shadow: rgba(50, 50, 93, 0.35) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.4) 0px 1px 3px -1px;
  border: none;
  margin-right: 15px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  text-align: center;
  width: 220px;
}

.title .stats .card span {
  font-size: 30px;
  font-weight: 600;
}

.title .stats .active {
  background-color: #006eff;
  color: #fff;
}

