.single-lesson-edit {
	background-color: rgba(0, 0, 0, 0.4);
	width: 100%;
	min-height: 100vh;
	padding: 50px 0;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1000;
	display: grid;
	place-items: center;
	cursor: default;
}

.single-lesson-editcard {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  width: 90vw;
  max-width: 500px;
  margin: auto;
  padding: 25px;
  position: relative;
  height: fit-content;
}

.single-lesson-edit .lesson-edit-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 20px;
}

.single-lesson-edit .lesson-edit-btn button {
	background-color: rgb(0, 140, 255);
	color: #fff;
	border-radius: 5px;
	border: none;
	outline: none;
	cursor: pointer;
	padding: 5px 10px;
	margin: 0 10px;
	font-size: 15px;
	font-weight: 500;
}

.single-lesson-edit h1 {
	margin-bottom: 10px;
}

.single-lesson-edit p {
	color: #000;
	font-size: 15px;
}

.single-lesson-edit textarea {
	width: 100%;
	margin-top: 5px;
	padding: 5px 10px;
	resize: vertical;
	height: 100px;
	outline: none;
	border: 1px solid rgba(0, 0, 0, 0.7);
	border-radius: 2px;
}

.single-lesson-edit input {
	outline: none;
	border: 1px solid rgba(0, 0, 0, 0.7);
	width: 100%;
	margin-top: 5px;
	padding: 5px 10px;
	border-radius: 2px;
}

.single-lesson-edit input[type='file'] {
	border: none;
	padding-left: 0;
}
